.navbarPage {
  position: relative;
  height: 75px;
  .header {
    position: fixed;
    background: white;
    width: 100%;
    height: 75px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 30px 20px;
    z-index: 1;
  }


  .large-e {
    object-fit: contain;
    height: 20%;
    width: 120px;
    margin-top: 0;
    margin-left: 0;
  }

  .btn-contained {
    background-color: var(--primary);
    border: solid var(--primary);
    padding: 6px 10px;
    border-radius: 20px;
    color: #fff;
    font-weight: bold;
    cursor: pointer;
    margin-right: 2rem;
  }

  .btn-contained:hover {
    background-color: #fff;
    color: var(--primary);
    border: 1px solid var(--primary);
    transition: 0.5s;
  }

  .btn-outline {
    color: var(--primary);
    background-color: #fff;
    border-radius: 20px;
    border-color: var(--primary);
    padding: 6px 10px;
    margin-right: 2px;
    font-weight: bold;
    cursor: pointer;
  }

  .hamburger {
    display: none;
  }

  .btn-outline:hover {
    background: var(--primary);
    color: #fff;
    transition: 0.5s;
  }

  .nav-menu {
    display: flex;
    text-align: right;
    /* border: 5px solid red; */
  }

  .nav-menu li {
    list-style: none;
    padding: 8px 12px;
  }

  .nav-menu a {
    font-size: 1.2rem;
    font-weight: 500;
  }

  .hamburger {
    display: none;
  }

  @media screen and (max-width: 768px) {
    .navbarPage {
      width: 100%;
      overflow-x: hidden;
      position: relative;
    }

    .hamburger {
      display: block;
      margin-right: 10px;
     
    }

    .nav-menu {
      position: absolute;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      top: 0;
      left: -100%;
      text-align: center;
      width: 100%;
      min-height: 100vh;
      transition: 0.5s;
      z-index: -1;
    }

    .nav-menu.active {
      left: 0;
      overflow: hidden;
    }

    .nav-menu li {
      padding: 1rem 0;
    }

    .nav-menu a {
      font-size: 2rem;
      color: white;
    }

    .btn-contained {
      border: none;
      background-color: white;
      color: var(--primary);
    }

    ul li:last-child {
      margin-left: 8vw;
    }

    .btn-outline {
      border: none;
      background-color: none;
    }

    .btn-outline:hover {
      background: none;
      color: none;
    }

    img {
      width: 60%;
    }
  }
}

