:root {
  --primary: #ff8323;
  --primary-variant: #db7002;
  --secondary: #ff5223;
  --secondary-variant: #b33919;
  --auxilliary-variant: #b33919;
  /* font sizes */
  --header-1: 64px;
  --header-2: 40px;
  --header-3: 32px;
  --header-4: 20px;
  --subtitle: 14px;
  --body: 16px;
  --small: 14px;
  --button-text: 10px;
  --mobile-font: 6px;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Montserrat";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-width: fit-content;
  scroll-behavior: smooth;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.active {
  color: var(--secondary);
  background-color: white;
}
